import { useState, useEffect, useRef } from "react";
import lyrics_nc from "./lyrics/nuestra_cancion";
import nuestra_cancion from "./assets/nuestra_cancion.mp3";

const arrImgs = new Array(45);
arrImgs.fill(0);

function App() {
  const [lyric, setLyric] = useState("Para Sandy Paola, el amor de mi vida ❤️");
  const [isVisible, setIsVisible] = useState(true);

  const audio = new Audio(nuestra_cancion);
  const card = useRef();

  const getRandomInt = (min, max) => {
    return Math.floor(Math.random() * (max - min) + min);
  };

  const showImage = () => {
    const images = document.querySelectorAll(".hide");
    images[getRandomInt(0, images.length)].classList.replace("hide", "show");
  };

  const play = () => {
    audio.play();
    lyrics_nc.forEach((ly, i) => {
      setTimeout(() => {
        showImage();
        setLyric(ly.lyrics);
        setIsVisible(false);
      }, ly.time * 1000);
    });
  };

  useEffect(() => {
    card.current.innerHTML = `<div class="card-border"><p class="card">${lyric}</p></div>`;
  }, [lyric]);

  return (
    <div className="app">
      <div className="gallery">
        {arrImgs.map((img, i) => (
          <div className="hide" key={i}>
            <img src={`./img/img (${i + 1}).jpg`} />
          </div>
        ))}
      </div>
      <div className="card-wrapper" ref={card}></div>
      {isVisible ? <button className="card-play" onClick={play}></button> : ""}
    </div>
  );
}

export default App;
